/* src/App.css */
.App {
  text-align: center;
  background-color: #99ccff; /* Dark background to match the editor */
  color: white; /* White text to contrast with the dark background */
}

.App-header {
  background-color: #282c34;
  padding: 10px;
  color: white;
}

.editor-container {
  display: flex;
  justify-content: space-around;
  margin: 20px;
}

.editor-container textarea {
  width: 45%;
  height: 100px;
  padding: 10px;
  background-color: #282c34; /* Match the textarea background to the editor */
  color: white; /* White text for textarea */
  border: none;
  border-radius: 4px;
}
